import api from './api.js'
import axios from 'axios'
import jwt from './jwt.js'
import router from '@/router'


const state = {
    intro: {},
    introLoading: true,
}
  
const mutations = {
    setIntro: (state, intro) => {
        state.intro = intro
        state.introLoading = false
    },
} 
  
const actions = {
    fetchIntro ({ commit, dispatch }) {
        return new Promise(function(resolve, reject) {
            axios.get(`${api.state.path}/api/intro`)
            .then (response => {
                    commit('setIntro', response.data)
                    resolve()
            })
            .catch (e => {
                dispatch('alert/newAlert', {type:"error", content:e.response.data}, {root:true})
                router.push({name:'error', params: {code:e.response.status}})
                reject()
            })
        })
    },
    updateIntro ({},intro) {
        return axios({
            method: 'post',
            url: `${api.state.path}/api/intro/edit`,
            headers: {
                Authorization: `Bearer ${jwt.state.current}`
            },
            data: {
                content: intro.content,
            }
        })
    }
}
  
const getters = {
    getIntro: state => state.intro,
    introLoading: state => state.introLoading,
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}