const state = {
    alerts: []
  }
  
  const mutations = {
    addAlert (state, alert) {
      if (state.alerts.length >= 2) {
        state.alerts.shift()
      }
      
      if (Array.isArray(alert.content)) {
        alert.content = alert.content.join("<br>")
      }
      // console.log(alert);
      state.alerts.push(alert)
    },
    delAlert (state) {
        state.alerts.shift()
    },
    resetAlert (state) {
      state.alerts = []
    }
  }
  
  const actions = {
    newAlert ({ commit }, { type, content, trash }) {
      commit('addAlert', { 'type': type, 'content': content, 'show': true, 'trash': trash})
      let del = setTimeout(()=>{commit('delAlert')}, 5000)
    },
    resetAlert ({ commit }) {
      commit('resetAlert')
    }
  }
  
  const getters = {
    alerts: state => state.alerts
  }
  
  export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
  }