import api from './api.js'
import axios from 'axios'
import jwt from './jwt.js'
import router from '@/router'


const state = {
    person: {},
    personLoading: true,
    artworks: [],
    artworksLoading: true,
    persons: [],
    personsLoading: true
}
  
const mutations = {
    setPerson: (state, person) => {
        state.person = person
        state.personLoading = false
    },
    setArtworks: (state, artworks) => {
        state.artworks = artworks
        state.artworksLoading = false
    },
    setPersons: (state, persons) => {
        state.persons = persons
        state.personsLoading = false
    },
    clearPerson: (state) => {
        state.person = {}
        state.personLoading = true
        state.artworks = {}
        state.artworksLoading = true
    }
} 
  
const actions = {
    clearPerson({ commit }) {
        commit('clearPerson')
    },
    fetchPerson ({ commit, dispatch }, person) {
        return new Promise(function(resolve, reject) {
            axios.get(`${api.state.path}/api/person/${person}`)
            .then (response => {
                    commit('setPerson', response.data)
                    resolve()
            })
            .catch (e => {
                dispatch('alert/newAlert', {type:"error", content:e.response.data}, {root:true})
                router.push({name:'error', params: {code:e.response.status}})
                reject()
            })
        })
    },
    fetchArtworks ({ commit, dispatch, state }, person) {
        return new Promise(function(resolve, reject) {
            axios.get(`${api.state.path}/api/person/${person}/artworks`)
            .then (response => {
                commit('setArtworks', response.data)
                resolve()
            })
            .catch (e => {
                dispatch('alert/newAlert', {type:"error", content:e.response.data}, {root:true})
                router.push({name:'error', params: {code:e.response.status}})
                reject()
            })
        })
    },
    fetchPersons ({ commit, dispatch }) {
        return new Promise(function(resolve, reject) {
            axios.get(`${api.state.path}/api/persons`)
            .then (response => {
                commit('setPersons', response.data)
                resolve()
            })
            .catch (e => {
                dispatch('alert/newAlert', {type:"error", content:e.response.data}, {root:true})
                router.push({name:'error', params: {code:e.response.status}})
                reject()
            })
        })
    },
    createPerson ({ }, person) {
        return axios({
            method: 'post',
            url: `${api.state.path}/api/person/new`,
            headers: {
                Authorization: `Bearer ${jwt.state.current}`
            },
            data: {
                slug: person.slug,
                name: person.name,
                state: person.state,
                img: person.img,
                date: person.date,
                bio: person.bio,
                notes: person.notes
            }
        })
    },
    updatePerson ({ },person) {
        return axios({
            method: 'post',
            url: `${api.state.path}/api/person/${person.slug}/edit`,
            headers: {
                Authorization: `Bearer ${jwt.state.current}`
            },
            data: {
                name: person.name,
                state: person.state,
                img: person.img,
                date: person.date,
                bio: person.bio,
                notes: person.notes
            }
        })
    },
    deletePerson ({ commit, dispatch },person) {
        return new Promise(function(resolve, reject) {
            axios({
                method: 'post',
                url: `${api.state.path}/api/person/${person}/delete`,
                headers: {
                    Authorization: `Bearer ${jwt.state.current}`
                }
            })
            .then(response => {
                dispatch('alert/newAlert', {type:"warning", content:`Placé dans la corbeille`, trash: true}, {root:true})
                commit('setPersons', response.data)
                resolve()
            })
            .catch(e=>{
                dispatch('alert/newAlert', {type:"error", content:e.response.data}, {root:true})
                reject()
            })
        })
    },
    deleteArtwork ({ commit, dispatch, state }, artwork) {
        return new Promise(function(resolve, reject) {
            axios({
                method: 'post',
                url: `${api.state.path}/api/artwork/${artwork.slug}/delete`,
                headers: {
                    Authorization: `Bearer ${jwt.state.current}`
                }
            })
            .then(response => {
                dispatch('alert/newAlert', {type:"warning", content:`Placé dans la corbeille`, trash: true}, {root:true})
                commit('setArtworks', response.data.filter(o => o.person === state.person.name))
                resolve()
            })
            .catch(e=>{
                dispatch('alert/newAlert', {type:"error", content:e.response.data}, {root:true})
                reject()
            })
        })
    }
}
  
const getters = {
    getPerson: state => state.person,
    personLoading: state => state.personLoading,
    getArtworks: state => state.artworks,
    artworksLoading: state => state.artworksLoading,
    getPersons: state => state.persons,
    personsLoading: state => state.personsLoading
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}