<template>
    <header> <!-- header : menu en haut de la page -->
      <img src="@/assets/icon/book.svg" class="book">
      <h1 class="title">Les p'tites bios</h1>
      <nav class="menu">
        <router-link to="/">Home</router-link>
        <router-link :to="'/'+getPerson.slug" v-if="getArtwork.person && getPerson.slug">{{getArtwork.person}}</router-link>
      </nav>
      <nav class="topBottom">
        <img src="@/assets/icon/up.svg" @click="up" :class="{'on':on !='top'}"><br>
        <img src="@/assets/icon/down.svg" @click="down"  :class="{'on': on!='bottom'}">
      </nav>
    </header> <!-- fin header -->
</template>
<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
        on: "top"
    }
  },
  computed: {
        ...mapGetters('jwt',[
        'jwt',
        'level',
        'jwtSubject',
        'jwtIssuer'
        ]),
        ...mapGetters('persons',[
        'getPerson',
        ]),
        ...mapGetters('artworks',[
        'getArtwork',
        ])
    },
    created() {
      document.onscroll = this.checkScroll
    },
    methods: {
      checkScroll() {
        if (window.scrollY < 200) {
          this.on = "top"
        } else if (window.scrollY > document.body.clientHeight-window.innerHeight-200) {
          this.on = "bottom"
        } else {
          this.on = "middle"
        }
      },
      up() {
        window.scrollTo(0, 0)
      },
      down() {
        window.scrollTo(0, document.body.clientHeight)
      }
    }

}
</script>
