import api from './api.js'
import jwt from './jwt.js'
import axios from 'axios'
import router from '@/router'



const state = {
    artwork: [],
    artworkLoading: true,
    artworks: [],
    artworksLoading: true,
  }
  
  const mutations = {
    setArtwork: (state, artwork) => {
        state.artwork = artwork
        state.artworkLoading = false
    },
    setArtworks: (state, artworks) => {
        state.artworks = artworks
        state.artworksLoading = false
    },
    clearArtwork: (state) => {
        state.artwork = {}
        state.artworkLoading = true
    }
  }
  
  const actions = {
    clearArtwork({ commit }) {
      commit('clearArtwork')
    },
    fetchArtwork ({ commit, dispatch}, artwork) {
      return new Promise(function(resolve, reject) {
        axios.get(`${api.state.path}/api/artwork/${artwork}`)
        .then (response => {
          commit('setArtwork', response.data)
          resolve()
        })
        .catch (e => {
          dispatch('alert/newAlert', {type:'error', content:e.response.data}, {root:true})
          router.push({name:'error', params: {code:e.response.status}})
          reject()
        })
      })
    },
    fetchArtworks ({ commit, dispatch }) {
      return new Promise(function(resolve, reject) {
        axios.get(`${api.state.path}/api/artworks`)
        .then (response => {
          commit('setArtworks', response.data)
          resolve()
        })
        .catch (e => {
          dispatch('alert/newAlert', {type:'error', content:e.response.data}, {root:true})
          reject()
        })
      })
    },
    createArtwork ({}, artwork) {
      return axios({
        method: 'post',
        url: `${api.state.path}/api/artwork/new`,
        headers: {
          Authorization: `Bearer ${jwt.state.current}`
        },
        data: {
          slug: artwork.slug,
          name: artwork.name,
          state: artwork.state,
          person: artwork.person,
          date: artwork.date,
          content: artwork.content,
          text: artwork.text,
          notes: artwork.notes
        }
      })
    },
    updateArtwork({}, editedArtwork) {
      return axios({
        method: 'post',
        url: `${api.state.path}/api/artwork/${editedArtwork.slug}/edit`,
        headers: {
          Authorization: `Bearer ${jwt.state.current}`
        },
        data: {
          name: editedArtwork.name,
          state: editedArtwork.state,
          person: editedArtwork.person,
          date: editedArtwork.date,
          content: editedArtwork.content,
          text: editedArtwork.text,
          notes: editedArtwork.notes
        }
      })
    },
    deleteArtwork ({ commit, dispatch }, artwork) {
      return new Promise(function(resolve, reject) {
        axios({
          method: 'post',
          url: `${api.state.path}/api/artwork/${artwork.slug}/delete`,
          headers: {
            Authorization: `Bearer ${jwt.state.current}`
          }
        })
        .then(response => {
          dispatch('alert/newAlert', {type:'warning', content:`Placé dans la corbeille`, trash: true}, {root:true})
          commit('setArtworks', response.data)
          resolve()
        })
        .catch(e=>{
          dispatch('alert/newAlert', {type:"error", content:e.response.data}, {root:true})
          reject()
        })
      })
    }
  }
  
  const getters = {
    getArtwork: state => state.artwork,
    artworkLoading: state => state.artworkLoading,
    getArtworks: state => state.artworks,
    artworksLoading: state => state.artworksLoading,
  }
  
  export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
  }