import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  root: '/',
  routes: [
    {
      path: '/',
      name: 'home',
      component: () => import('@/views/Home.vue')
    },
    {
      path: '/legal',
      name: 'legal',
      component: () => import('@/views/Info/Legal.vue')
    },
    {
      path: '/badges',
      name: 'badges',
      component: () => import('@/views/Info/Badges.vue')
    },
    {
      path: '/contact',
      name: 'contact',
      component: () => import('@/views/Info/Contact.vue')
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('@/views/Auth/Login.vue')
    },
    {
      path: '/register',
      name: 'register',
      component: () => import('@/views/Auth/Register.vue')
    },
    {
      path: '/trash',
      name: 'trash',
      component: () => import('@/views/Trash/Show.vue')
    },
    {
      path: '/new/artwork',
      name: 'newArtwork',
      meta: {needAuth:true},
      component: () => import('@/views/Artwork/New.vue')
    },
    {
      path: '/new/person',
      name: 'newPerson',
      meta: {needAuth:true},
      component: () => import('@/views/Person/New.vue')
    },
    {
      path: '/error/:code',
      name: 'error',
      component: () => import('@/views/Error.vue')
    },
    {
      path: '/edit/intro',
      name: 'editIntro',
      meta: {needAuth:true},
      component: () => import('@/views/Intro/Edit.vue')
    },
    {
      path: '/artwork/:artwork',
      name: 'artwork',
      component: () => import('@/views/Artwork/Show.vue')
    },
    {
      path: '/artwork/:artwork/edit',
      name: 'editArtwork',
      component: () => import('@/views/Artwork/Edit.vue')
    },
    {
      path: '/:person',
      component: () => import('@/views/Person/Index.vue'),
      children: [
        {path:'', component: () => import('@/views/Person/Show.vue')},
        {path:'edit', meta: {needAuth:true}, component: () => import('@/views/Person/Edit.vue')},
        {path: 'print', component: () => import('@/views/Person/Print.vue')},
        {path: ':artwork', component: () => import('@/views/Artwork/Index.vue'), children: [
          {path:'', component: () => import('@/views/Artwork/Show.vue')},
          {path:'edit', meta: {needAuth:true}, component: () => import('@/views/Artwork/Edit.vue')},
          {path: 'print', component: () => import('@/views/Artwork/Print.vue')}
        ]}
      ]
    }
  ]
})
