<template>
    <div class="flash">
        <div :class="alert.type+' flash__message'" v-show="alert.show" v-for="alert in alerts" :key="alert.id">
            <div class="flash__message-content" v-html="alert.content"></div>
            <router-link :to="{name: 'trash'}" v-if="alert.trash" class="flash__action">  
              <button >Ouvrir</button>
            </router-link>
        </div>
    </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters('alert', [
      'alerts'
    ]),
    ...mapGetters('jwt',[
        'jwt'
    ])
  }
}
</script>
<style>

.flash__message {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 15px 30px 15px 15px;
  margin-bottom: 10px;
  border: 1px solid transparent;
  border-radius: 10px;
  background-color: #cccccc;
  position: relative;
  /* box-shadow: #999 0px 3px 10px; */
 
}

.flash__message.success {
  color: #3c763d;
  background-color: rgba(223, 240, 216, 1);
  border-color: #d6e9c6;
}

.flash__message.warning {
  color: #8a6d3b;
  background-color: rgb(252, 248, 227);
  border-color: #faebcc;
}

.flash__message.error {
  color: #a94442;
  background-color: rgb(242, 222, 222);
  border-color: #ebccd1;
}

.flash__message.info {
  color: #37516A;
  background-color: #F2F5FE;
  border-color: rgb(223, 227, 240);
}

.flash__close-button {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
  top: 0px;
  right: 0px;
  color: inherit;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: transparent;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 18px;
  line-height: 10px;
  margin: 0;
  padding: 8px;
}

.flash__close-button:active,.flash__close-button:focus {
  outline: none;
}

.flash__action, .flash__action:hover {
  border: none;
  width: 100%;
  cursor: initial;
  text-align: center;
}
.flash__action::after, .flash__action:hover::after {
  display: none;
}
.flash__action button {
  background: none;
  border: 1px rgba(0,0,0,0.3) solid;
  padding: 10px 20px;
  margin-top: 20px;
  border-radius: 7px;
  color: rgba(0,0,0, 0.7);
  float: right;
  cursor: pointer;
  outline: none;
}
.flash__action button:hover {
  border: 1px rgba(0,0,0,0.5) solid;
  color: rgba(0,0,0, 1);

}


</style>