import api from './api.js'
import jwt from './jwt.js'
import axios from 'axios'
import router from '@/router'



const state = {
    trash: [],
    trashLoading: true
}
  
const mutations = {
    setTrash: (state, trash) => {
        state.trash = trash
        state.trashLoading = false
    }
}
  
const actions = {
    fetchTrash ({ commit, dispatch}) {
        return new Promise(function(resolve, reject) {
            axios({
                method: 'get',
                url: `${api.state.path}/api/trash`,
                headers: {
                    Authorization: `Bearer ${jwt.state.current}`
                }
            })
            .then (response => {
                commit('setTrash', response.data)
                resolve()
            })
            .catch (e => {
                dispatch('alert/newAlert', {type:'error', content:e.response.data}, {root:true})
                router.push({name:'error', params: {code:e.response.status}})
                reject()
            })
        })
    },
    restoreElement({ commit, dispatch }, id) {
        return new Promise(function(resolve, reject) {
            axios({
                method: 'post',
                url: `${api.state.path}/api/trash/${id}`,
                headers: {
                    Authorization: `Bearer ${jwt.state.current}`
                }
            })
            .then(response => {
                dispatch('alert/newAlert', {type:'success', content:'Element restauré'}, {root:true})
                commit('setTrash', response.data)
                resolve()
            })
            .catch(e=>{
                dispatch('alert/newAlert', {type:"error", content:e.response.data}, {root:true})
                reject()
            })
        })
    },
    deleteElement ({ commit, dispatch }, id) {
        return new Promise(function(resolve, reject) {
            axios({
                method: 'delete',
                url: `${api.state.path}/api/trash/${id}`,
                headers: {
                    Authorization: `Bearer ${jwt.state.current}`
                }
            })
            .then(response => {
                dispatch('alert/newAlert', {type:'error', content:'Element supprimé'}, {root:true})
                commit('setTrash', response.data)
                resolve()
            })
            .catch(e=>{
                dispatch('alert/newAlert', {type:"error", content:e.response.data}, {root:true})
                reject()
            })
        })
    }
}
  
const getters = {
    getTrash: state => state.trash,
    trashLoading: state => state.trashLoading,
}
  
export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}